.filter {

}

.filter__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@media (min-width: 768px) {
  .filter__wrapper {
    width: 640px;
    margin: 0 auto;
  }

  /*.filter__nav-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }*/
}

@media (min-width: 1200px) {
  .filter {
    height: 259px;
    border-bottom: 3px solid #2593f9;
    background-color: #262f3f;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-image: url('../../static/img/gallery-preview.svg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: 15% 20px;
  }

  .filter__wrapper {
    width: 960px;
    padding-top: 130px;
  }

  .filter__title {
    margin: 0;
    margin-bottom: 49px;
    padding: 0;
    font-size: 42px;
    line-height: 38px;
    font-weight: 700;
    font-style: italic;
    text-align: right;
    text-transform: uppercase;
  }

  .filter__nav-list {
    position: relative;
    min-height: 42px;
  }

  .filter__nav-item {
    position: absolute;
  }

  .filter__nav-item:nth-child(1) {
    bottom: 0;
    left: 0;
  }

  .filter__nav-item:nth-child(2) {
    bottom: 0;
    left: 160px;
  }

  .filter__nav-item:nth-child(3) {
    bottom: 0;
    left: 380px;
  }

  .filter__nav-item:nth-child(4) {
    bottom: 0;
    left: 600px;
  }

  .filter__nav-item:nth-child(5) {
    bottom: 0;
    left: 794px;
  }

  .filter__button {
    margin: 0;
    padding: 11px 20px;
    font-size: 15px;
    line-height: 18px;
    font-weight: 700;
    font-style: italic;
    border: none;
    background-color: transparent;
    color: #ffffff;
    cursor: pointer;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    outline: none;
  }

  .filter__button--active {
    padding: 7px 17px;
    border: 3px solid #2593f9;
    border-bottom-color: #ffffff;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
    background-color: #ffffff;
    color: #000000;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
}


