.page-footer {
  background-color: #090b16;
}

@media (min-width: 768px) {
  .page-footer__wrapper {
    width: 640px;
    margin: 0 auto;
  }
}

@media (min-width: 1200px) {
  .page-footer {
    padding: 60px 0 50px 0;
  }

  .page-footer__wrapper {
    width: 960px;
  }

  .page-footer__wrapper-two {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin-bottom: 65px;
  }

  .page-footer__logo {
    width: 261.04296875px;
    height: 149px;
    background-image: url('../../static/img/1bottom-dark.svg');
    background-position: 0 0;
    background-repeat: no-repeat;
  }

  .page-footer__wrapper-btn {
    padding-top: 30px;
  }

  .page-footer__btn {
    margin-bottom: 25px;
    padding: 7px 10px 5px 10px;
    font-size: 14px;
    line-height: 18px;
    color: #a0a0a0;
    text-align: center;
    border: 1px solid #a0a0a0;
    background-color: transparent;
  }

  .page-footer__btn:hover {
    background-color: rgba(255, 255, 255, 0.95);
    color: #090b16;
    /*outline-radius: ;*/
  }

  .page-footer__btn:focus {
    background-color: rgba(255, 255, 255, 0.8);
    color: rgba(0, 0, 0, 0.8);
  }

  .page-footer__city {
    position: relative;
    margin: 0;
    padding: 0;
    padding-left: 25px;
    font-size: 15px;
    line-height: 17px;
    color: #a0a0a0;
  }

  .page-footer__city::before {
    position: absolute;
    content: '';
    top: -3px;
    left: 0;
    width: 15px;
    height: 17px;
    background-image: url('../../static/img/placeholder.svg');
    background-repeat: no-repeat;
    background-position: 0 0;
  }

  .page-footer__wrapper-free {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }

  .page-footer__copyright {
    margin: 0;
    padding: 0;
    font-size: 14px;
    line-height: 17px;
    text-transform: uppercase;
    color: #a0a0a0;
  }

  .page-footer__copyright--small {
    font-size: 12px;
    line-height: 17px;
  }

  .page-footer__develop {
    margin: 0;
    padding: 0;
    text-transform: uppercase;
    color: #a0a0a0;
  }

  .page-footer__develop--small {
    font-size: 14px;
    line-height: 17px;
    text-transform: none;
  }

  .page-footer__develop a {
    font-size: 12px;
    line-height: 17px;
    color: #a0a0a0;
  }

  .page-footer__develop a:hover {
    text-decoration: none;
    color: #ffffff;
  }

  .page-footer__develop a:focus {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.7);
  }
}


