

@media (min-width: 768px) {
  .prices-preview__wrapper {
    width: 640px;
    margin: 0 auto;
  }
}

@media (min-width: 1200px) {
  .prices-preview {
    height: 220px;
    background-color: #262f3f;
  }

  .prices-preview__wrapper {
    width: 960px;
    /*height: 100%;*/
    padding-top: 130px;
    /*background-image: url('../img/gallery-preview.svg');*/
    /*background-repeat: no-repeat;*/
    /*background-attachment: fixed;*/
    /*background-position: 0 0;*/
  }

  .prices-preview__title {
    position: relative;
    margin: 0;
    /*margin-top: 129px;*/
    padding: 0;
    font-size: 42px;
    line-height: 38px;
    font-weight: 700;
    font-style: italic;
    text-align: right;
    text-transform: uppercase;
  }

  .prices-preview__title::before {
    position: absolute;
    content: '';
    top: -115px;
    left: 0;
    z-index: 10;
    width: 260px;
    height: 250px;
    /*margin-left: 125px;*/
    background-image: url('../../static/img/price-preview.svg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: 161px 20px;
  }
}


