.advantages {
  padding-top: 50px;
  background-color: #ffffff;
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.advantages__item-title {
  margin: 0;
  padding: 0;
  color: #000000;
}

.advantages__item-paragraph {
  margin: 0;
  padding: 0;
  color: #747474;
}

@media (min-width: 768px) {
  .advantages__wrapper {
    width: 640px;
    margin: 0 auto;
  }
}

@media (min-width: 1200px) {
  .advantages {
    padding-top: 250px;
    padding-bottom: 40px;
    background-image: url('../../static/img/preview5.jpg');
    background-position: center 30px;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: auto 490px;
  }

  .advantages__wrapper {
    width: 960px;
  }

  .advantages__list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  .advantages__item {
    position: relative;
    width: 240px;
    padding-top: 80px;
    padding-left: 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }

  .advantages__item::before {
    position: absolute;
    top: 15px;
    left: 75px;
    font-size: 45px;
    line-height: 48px;
    font-weight: 500;
    color: #424242;
    -webkit-transform: scaleY(1.4);
        -ms-transform: scaleY(1.4);
            transform: scaleY(1.4);
  }

  /*.advantages__item:nth-child(1) {
    &::before {
      content: '01';
    }
  }

  .advantages__item:nth-child(2) {
    &::before {
      content: '02';
    }
  }

  .advantages__item:nth-child(3) {
    &::before {
      content: '03';
    }
  }

  .advantages__item:nth-child(4) {
    &::before {
      content: '04';
    }
  }*/

  .advantages__item-title {
    position: relative;
    z-index: 1;
    margin-bottom: 21px;
    font-size: 22px;
    line-height: 26px;
    font-weight: 400;
    text-transform: uppercase;
  }

  .advantages__item-title::before {
    content: '';
    position: absolute;
    top: -18px;
    left: -33px;
    z-index: -1;
    width: 257px;
    height: 55px;
    background-image: url('../../static/img/advantages-item-bg.png');
    background-position: 0 0;
    background-repeat: no-repeat;
  }

  .advantages__item-paragraph {
    font-size: 16px;
    line-height: 21px;
    font-weight: 500;
    color: #000000;
  }

  .advantages__item-paragraph--marker {
    padding: 2px 3px;
    border-radius: 3px;
    background-color: rgba(37, 147, 249, 0.4);
  }
}


