@media (min-width: 768px) {
  .prices__wrapper {
    width: 640px;
    margin: 0 auto;
  }
}

@media (min-width: 1200px) {
  .prices {
    padding: 25px 0 50px 0;
  }

  .prices__wrapper {
    width: 960px;
  }

  .prices__table {
    margin: 0;
    padding: 0;
    font-size: 16px;
    line-height: 20px;
    font-weight: 500;
    color: #000000;
    border-collapse: collapse;
  }

  .prices__table-tr {
    margin: 0;
    padding: 0;
    background-color: #ffffff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }

  .prices__table-tr--dark {
    background-color: #ececec;
  }

  .prices__table-tr:hover {
    outline: 1px solid #2593f9;
    outline-offset: -1px;
  }

  .prices__table-tr--title:hover {
    outline: none;
  }

  .prices__table-th {
    margin: 0;
    padding: 6px 8px;
    padding-top: 25px;
    font-size: 19px;
    line-height: 22px;
    font-weight: 400;
    text-transform: uppercase;
  }

  .prices__table-td {
    margin: 0;
    padding: 8px 40px 7px 8px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }

  .prices__table-td--prices {
    width: 150px;
    padding: 8px 8px 7px 8px;
    font-size: 17px;
    text-align: center;
  }

  .prices__table-rub {
    font-weight: 500;
    font-size: 15px;
  }
}


