.gallery__template {
  display: none;
}

.gallery {
  background-color: #ffffff;
}

@media (min-width: 768px) {
  .gallery__wrapper {
    width: 640px;
    margin: 0 auto;
  }

  .gallery__list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style: none;
  }
}

@media (min-width: 1200px) {
  .gallery {
    min-height: 148px;
    padding: 50px 0 40px 0;
  }

  .gallery__wrapper {
    width: 960px;
  }

  /*.gallery__loader {
    padding-top: 10px;
    text-align: center;
  }*/

  /*.gallery__loader-text {
    margin: 0;
    margin-bottom: 15px;
    padding: 0;
    font-size: 15px;
    line-height: 17px;
    text-transform: uppercase;
    color: #000000;
  }*/

  .gallery__list-item-job {
    position: relative;
    display: block;
    width: 184px;
    height: 138px;
    margin-right: 10px;
    margin-bottom: 10px;
    text-decoration: none;
    color: #000000;
    background-color: gray;
    /*background-image: url('../../static/img/gallery-preview/manicure/2-job-preview-desktop.jpg');*/
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover;
  }

  .gallery__list-item-job::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    z-index: 10;
    display: none;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.45);
  }

  .gallery__list-item-job::after {
    position: absolute;
    content: '';
    top: 5px;
    right: 5px;
    z-index: 15;
    display: none;
    width: 28px;
    height: 28px;
    background-image: url('../../static/img/full-size.svg');
    background-position: 0 0;
    background-repeat: no-repeat;
  }

  .gallery__list-item-job:hover::before,
  .gallery__list-item-job:focus::before,
  .gallery__list-item-job:hover::after,
  .gallery__list-item-job:focus::after {
    display: block;
  }

  .gallery__list-item:nth-child(5n) .gallery__list-item-job {
    margin-right: 0;
  }
}


