.page-header {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 50px;
}

.page-header--scroll {
  /*background-color: rgba(255, 255, 255, 0.8);*/
  -webkit-box-shadow: 0 -5px 22px 0 #000000;
          box-shadow: 0 -5px 22px 0 #000000;
}

.page-header__nav-top {
  position: absolute;
  z-index: 200;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #ececec;
}

.page-header__wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 50px;
  padding: 0 10px;
  margin: 0;
}

.page-header__wrapper-one {
  display: -ms-flexbox;
  display: flex;
}

.page-header__feedback {
  width: 36px;
  height: 30px;
  margin: 0;
  margin-top: 9px;
  margin-right: 12px;
  padding: 0;
  /*padding-top: 1px;*/
  /*font-size: 0;*/
  border: 2px solid #607282;
  border-radius: 5px;
  background-image: url('../../static/img/mail.svg');
  background-position: 5px 2px;
  background-repeat: no-repeat;
  cursor: pointer;
  background-color: transparent;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.social-button {
  display: block;
  height: 30px;
  margin: 0;
  margin-top: 9px;
  padding: 0;
}

.page-header__toggle {
  display: block;
  width: 50px;
  height: 34px;
  margin: 0;
  margin-top: 7px;
  padding: 0;
  font-size: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
}

.page-header__toggle:hover,
.page-header__toggle:focus {
  background-color: #e8e5e5;
}

.page-header__toggle-hamburger {
  position: relative;
  left: 50%;
  display: block;
  width: 40px;
  height: 2px;
  margin: 0;
  margin-left: -20px;
  padding: 0;
  background-color: #607282;
  -webkit-transition: 0.23s;
  -o-transition: 0.23s;
  transition: 0.23s;
}

.page-header__toggle-hamburger::before,
.page-header__toggle-hamburger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 40px;
  height: 2px;
  background-color: #607282;
  -webkit-transition: 0.23s;
  -o-transition: 0.23s;
  transition: 0.23s;
}

.page-header__toggle-hamburger::before {
  top: -9px;
}

.page-header__toggle-hamburger::after {
  top: 9px;
}

.page-header__nav--opened .page-header__toggle-hamburger {
  height: 0;
  width: 0;
  margin-left: -21px;
}

.page-header__nav--opened .page-header__toggle-hamburger::before,
.page-header__nav--opened .page-header__toggle-hamburger::after {
  content: "";
  position: absolute;
  top: -2px;
  left: 2px;
  width: 37px;
  height: 2px;
  background-color: #607282;
}

.page-header__nav--opened .page-header__toggle-hamburger::before {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.page-header__nav--opened .page-header__toggle-hamburger::after {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.page-header__nav {
  position: absolute;
  z-index: 100;
  top: 50px;
  left: 0;
  width: 100%;
  height: auto;
  background-color: #e8e5e5;
}

.page-header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@media (min-width: 768px) {
  .page-header__wrapper {
    width: 640px;
    margin: 0 auto;
  }

  .page-header__nav-wrapper {
    width: 640px;
    margin: 0 auto;
  }

  .page-header__feedback {
    margin-right: 20px;
  }

  .page-header__nav-list {
    display: -ms-flexbox;
    display: flex;
  }
}

@media (min-width: 1200px) {
  .page-header__wrapper {
    position: relative;
    width: 960px;
  }

  .page-header__nav-wrapper {
    width: 960px;
  }

  .page-header__feedback {
    /*width: 130px;*/
    margin-right: 15px;
    /*padding-left: 32px;*/
    /*font-size: 14px;*/
    /*font-weight: 600;*/
    /*text-transform: uppercase;*/
    /*color: #607282;*/
    background-position: 5px 2px;
  }

  .page-header__popup-info {
    position: absolute;
    top: calc(100%);
    left: 6px;
    z-index: 20;
    display: none;
    padding: 7px 8px 7px 8px;
    font-size: 12px;
    line-height: 15px;
    font-weight: 500;
    color: #ffffff;
    text-transform: none;
    border-radius: 5px;
    background-color: rgba(37,147,249,.9);
  }

  .page-header__popup-info::before {
    content: '';
    position: absolute;
    left: 15px;
    top: -8px;
    width: 0;
    height: 0;
    border-right: 8px solid transparent;
    border-left: 8px solid transparent;
    border-bottom: 8px solid rgba(37,147,249,.9);
  }

  .page-header__feedback:hover .page-header__popup-info--email,
  .page-header__feedback:focus .page-header__popup-info--email {
    display: block;
  }

  .social-button:hover .page-header__popup-info--insta,
  .social-button:focus .page-header__popup-info--insta {
    display: block;
    left: 54px;
  }

  .page-header__nav {
    /*position: absolute;*/
    /*z-index: 10;*/
    /*top: 100%;*/
    /*left: 0;*/
    /*height: auto;*/
    padding: 10px 0;
    -webkit-transition-duration: 0.2s;
         -o-transition-duration: 0.2s;
            transition-duration: 0.2s;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    -webkit-transition-timing-function: ease-out;
         -o-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
  }

  .page-header__nav-item-link {
    display: block;
    height: 29px;
    margin: 0;
    margin-right: 45px;
    padding: 0;
    padding-top: 6px;
    padding-left: 25px;
    font-size: 17px;
    line-height: 19px;
    color: #607282;
    text-decoration: none;
    background-repeat: no-repeat;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }

  .page-header__nav-item-link--active {
    color: #000000;
  }

  .page-header__nav-item:nth-child(1) .page-header__nav-item-link {
    background-image: url('../../static/img/nail.svg');
    background-position: 0 3px;
  }

  .page-header__nav-item:nth-child(2) .page-header__nav-item-link {
    padding-left: 28px;
    background-image: url('../../static/img/eyelashes.svg');
    background-position: 0 4px;
  }

  .page-header__nav-item:nth-child(3) .page-header__nav-item-link {
    padding-left: 28px;
    background-image: url('../../static/img/eye-shadow.svg');
    background-position: 0 3px;
  }

  .page-header__nav-item:nth-child(4) .page-header__nav-item-link {
    padding-left: 29px;
    background-image: url('../../static/img/cream.svg');
    background-position: 0 2px;
  }

  .page-header__nav-item:nth-child(5) .page-header__nav-item-link {
    padding-left: 29px;
    background-image: url('../../static/img/gallery-menu.svg');
    background-position: 0 2px;
  }

  .page-header__nav-item:nth-child(6) .page-header__nav-item-link {
    padding-left: 27px;
    background-image: url('../../static/img/price-tag-menu.svg');
    background-position: 0 2px;
  }

  .page-header__nav-item--active .page-header__nav-item-link {
    position: relative;
  }

  .page-header__nav-item--active .page-header__nav-item-link::after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 0;
    right: 0;
    height: 4px;
    background-color: #2593f9;
  }

  .page-header__nav--closed .page-header__nav {
    /*opacity: 0;*/
    -webkit-transform: translateY(-120%);
        -ms-transform: translateY(-120%);
            transform: translateY(-120%); 
  }

  .page-header__nav--opened .page-header__nav {
    /*opacity: 1;*/
    -webkit-transform: translateY(0%);
        -ms-transform: translateY(0%);
            transform: translateY(0%);
    -webkit-box-shadow: 0 -5px 22px 0 #000000;
            box-shadow: 0 -5px 22px 0 #000000;
  }

  .page-header__logo {
    width: 200px;
    height: 40px;
    margin-top: 8px;
    background-image: url('../../static/img/1top-dark.svg');
    background-position: 0 0;
    background-repeat: no-repeat;
  }
}


