.modal-job {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 500;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  text-align: center;
  overflow: auto;
  white-space: nowrap;
}

.modal-job::after {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

@media (min-width: 768px) {
  
}

@media (min-width: 1200px) {
  .modal-job__popup {
    display: inline-block;
    position: relative;
    /*top: 0;*/
    /*left: 50%;*/
    width: 640px;
    height: auto;
    /*margin-left: -320px;*/
    margin: 20px 0;
    /*padding: 10px;*/
    background-color: #ffffff;
    vertical-align: middle;    
  }

  .modal-job__wrapper-one {
    width: 100%;
    height: 480px;
  }

  .modal-job__wrapper-two {
    padding: 10px;
    text-align: left;
  }

  .modal-job__description {
    margin: 0;
    margin-bottom: 15px;
    padding: 0;
    font-size: 16px;
    line-height: 18px;
    font-weight: 500;
    color: #000000;
  }

  .modal-job__category {
    position: relative;
    margin: 0;
    padding: 0;
    padding-left: 30px;
    font-size: 14px;
    line-height: 16px;
    font-weight: 500;
    color: #000000;
  }

  .modal-job__category::before {
    position: absolute;
    content: '';
    top: -4px;
    left: 0;
    width: 25px;
    height: 25px;
    background-image: url('../../static/img/pie-chart.svg');
    background-position: 0 0;
    background-repeat: no-repeat;
  }

  .modal-job__close {
    position: absolute;
    top: -4px;
    right: -50px;
    width: 40px;
    height: 40px;
    font-size: 0;
    background-color: transparent;
    border: 0;
    cursor: pointer;
  }

  .modal-job__close::before,
  .modal-job__close::after {
    content: '';
    position: absolute;
    top: 15px;
    left: 5px;
    width: 34px;
    height: 3px;
    background-color: #ffffff;
  }

  .modal-job__close::before {
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
  }

  .modal-job__close::after {
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
}


