body {
  font-size: 14px;
  line-height: 18px;
  font-family: "Muller", Arial, sans-serif;
  color: #ffffff;
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.025em;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  font-size: 16px;
  line-height: 23px;
  font-weight: 400;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 5px;
}

.hidden {
  display: none;
}

.body-noscroll {
  overflow: hidden;
}



