.services {
  padding: 70px 0 52px 0;
  background-color: #ffffff;
}

.services--dark {
  background-color: #262f3f;
}

.services__wrapper {
  position: relative;
}

.services__title {
  color: #3f495a;
}

.services__title--dark {
  color: #8795a8;
}

.services__title span {
  color: #2593f9;
}

.services__title--dark span {
  color: #feffff;
}

@media (min-width: 768px) {
  .services__wrapper {
    width: 640px;
    margin: 0 auto;
  }
}

@media (min-width: 1200px) {
  .services__wrapper {
    width: 960px;
  }

  .services__wrapper-one {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  .services__title {
    position: absolute;
    top: 145px;
    left: 675px;
    margin: 0;
    padding: 0;
    font-size: 68px;
    line-height: 70px;
    font-weight: 700;
    text-transform: uppercase;
  }

  .services__number {
    position: relative;
    width: 480px;
    margin: 0;
    margin-right: 115px;
    padding: 0;
    padding-top: 2px;
    padding-left: 15px;
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
    color: #3f495a;
  }

  .services__number::before {
    content: '';
    position: absolute;
    top: 8px;
    left: 44px;
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background-color: #2593f9;
  }

  .services__number--dark {
    color: #ffffff;
  }

  .services__number-big {
    position: relative;
    width: auto;
    margin: 0;
    padding: 0;
    font-size: 50px;
    line-height: 67px;
    font-weight: 700;
    color: #2593f9;
    -webkit-transform: rotate(-90deg) scaleY(1.2);
        -ms-transform: rotate(-90deg) scaleY(1.2);
            transform: rotate(-90deg) scaleY(1.2);
  }

  .services__jobs {
    position: relative;
    margin: 0;
    margin-left: 84px;
    padding: 0;
    padding-top: 2px;
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
    color: #3f495a;
  }

  .services__jobs::before {
    content: '';
    position: absolute;
    top: 4px;
    left: -15px;
    width: 1px;
    height: 47px;
    background-color: #908f8f;
  }

  .services__jobs::after {
    content: '';
    position: absolute;
    top: 4px;
    left: -75px;
    width: 1px;
    height: 108px;
    background-color: #908f8f;
  }

  .services__jobs--dark {
    color: #7a8799;
  }

  .services__jobs-numbers {
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
    text-transform: uppercase;
    color: #3f495a;
  }

  .services__jobs-numbers--dark {
    color: #a3b4cc;
  }

  .services__title-two {
    width: 480px;
    margin: 0;
    margin-bottom: 8px;
    padding: 0;
    padding-left: 15px;
    font-size: 32px;
    line-height: 36px;
    font-weight: 700;
    color: #3f495a;
    text-transform: uppercase;
  }

  .services__title-two--dark {
    color: #ffffff;
  }

  .services__title-free {
    width: 480px;
    margin: 0;
    margin-bottom: 45px;
    padding: 0;
    padding-left: 15px;
    font-size: 24px;
    line-height: 28px;
    font-weight: 500;
    color: #3f495a;
    text-transform: uppercase;
  }

  .services__title-free--dark {
    color: #8795a8;
  }

  .services__description {
    width: 520px;
    min-height: 200px;
    margin: 0;
    padding: 0;
    padding-left: 15px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 500;
    color: #3f495a;
  }

  .services__description--dark {
    color: #8795a8;
  }

  .services__description--cosmet {
    width: 540px;
  }

  .services__loader {
    height: 295px;
    padding-top: 80px;
    padding-bottom: 80px;
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }

  .services__loader--page-jobs {
    height: 190px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin: auto; /* контейнер в котором находится лоадер задан как флекс, поэтому центруем таким способом */
  }

  .services__loader--modal-job {
    height: 480px;
    padding-top: 170px;
    padding-bottom: 20px;
  }

  .services__loader-text {
    margin: 0;
    margin-bottom: 23px;
    padding: 0;
    font-size: 15px;
    line-height: 16px;
    color: #ffffff;
    text-transform: uppercase;
  }

  .services__loader-text--dark {
    color: #000000;
  }

  .services__slider {
    height: 235px;
    margin: 0;
    margin-bottom: 54px;
    padding: 50px 0 0 0;
    list-style: none;
  }

  /*.slick-track {
    transform: translate3d(-698px, 0px, 0px) !important;
  }*/

  .services__slide {
    /*width: 145px;*/
    /*height: 95px;*/
    background-color: #666666;
  }

  /*.slick-list {
    padding: 0 75px !important;
  }*/

  .slick-slide {
    width: 175px !important;
    height: 115px;
    margin: 17px 20px 0 20px;
    -webkit-transition-duration: 0.3s;
         -o-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-transition-property: height, width, margin;
    -o-transition-property: height, width, margin;
    transition-property: height, width, margin;
    opacity: 0.7;
  }

  .slick-slide img {
    height: 100%;
    width: 100%;
    -webkit-transition-duration: 0.3s;
         -o-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-transition-property: height, width;
    -o-transition-property: height, width;
    transition-property: height, width;
  }

  .slick-center {
    width: 214px !important;
    height: 150px;
    margin: 0;
    opacity: 1;
  }

  .slick-center img {
    /*height: 100%;*/
    /*width: auto;*/
    height: 150px;
  }

  .slick-dotted.slick-slider {
    margin-bottom: 60px;
  }

  .slick-dots li button {
    width: 16px;
    height: 16px;
    margin-left: 2px;
    margin-top: 2px;
    /*border: 1px solid #2593f9;*/
    border-radius: 50%;
    background-color: #2593f9;
    opacity: 1;
    -webkit-transition: width height 0.3s;
    -o-transition: width height 0.3s;
    transition: width height 0.3s;
  }

  .slick-dots li button::before {
    content: '';
    width: 0;
    height: 0;
    opacity: 1;
  }

  .slick-dots li button:hover,
  .slick-dots li button:focus/*,
  .slick-dots li.slick-active button*/ {
    opacity: 0.8;
  }

  .slick-dots li.slick-active button {
    width: 20px;
    height: 20px;
    margin-left: 0;
    margin-top: 0;
    opacity: 1;
  }

  .slick-dots li.slick-active button::before {
    content: '';
    top: 50%;
    left: 50%;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    margin: -4px 0 0 -4px;
    background-color: #ffffff;
    opacity: 1;
  }

  .services__wrapper-two {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    /*padding: 0 15px;*/
  }

  .services__jobs-btn {
    width: 190px;
    height: 40px;
    margin: 0;
    padding: 0;
    padding-left: 65px;
    padding-top: 10px;
    background-color: #2593f9;
    background-image: url('../../static/img/gallery.svg');
    background-position: 15px 2px;
    background-repeat: no-repeat;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }

  .services__price-btn {
    width: 135px;
    height: 40px;
    margin: 0;
    padding: 0;
    padding-left: 65px;
    padding-top: 10px;
    background-color: #3f495a;
    background-image: url('../../static/img/price-tag.svg');
    background-position: 15px 4px;
    background-repeat: no-repeat;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }

  .services__price-btn--dark {
    color: #262f3f;
    background-color: #ffffff;
    background-image: url('../../static/img/price-tag-dark.svg');
  }
}


